@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.App {
  --header_height: 60px;
  /*max-width:  calc(1140px + 15px * 2);*/
  /*padding-inline: 15px;*/
  /*margin: 0 auto;*/
}

.hwWrapper {
  max-width:  calc(1140px + 15px * 2);
  padding-inline: 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 370px;
  padding-top: 40px;
  gap: 25px;
  border-bottom: 1px solid #d9d9d9;
}

.hw {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.hw > div:first-child {
  display: flex;
  flex-direction: column;

  width: 100%;
}

.hwTitle {
  font-weight: 600;
  font-size: 22px;
  color: var(--color);
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 10px;
}